import React from 'react';
import Banner from './Banner';
import Slider from 'react-slick';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'block',
        width: '300px',
    },
}));

const Carousel = () => {
    const classes = useStyles();
    const settings = {
        className: "carouesl-home",
        dots: false,
        arrows: false,
        autoplay: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        draggable: false
    };

    const items = [{ type: 'side-banner', id: Math.floor(Math.random() * 1000) }, { id: Math.floor(Math.random() * 1000), type: 'footer-banner' }, { id: Math.floor(Math.random() * 1000), type: 'footer-banner', num: 2 }, { id: Math.floor(Math.random() * 1000), type: 'footer-banner', num: 3 }, { id: Math.floor(Math.random() * 1000), type: 'top-banner' }];

    return (
        <div className={classes.root}>
            <Slider {...settings}>
                {items.map(item => <Banner key={item.id} type={item.type} num={item.num}></Banner>)}
            </Slider>
        </div>
    )
}

export default Carousel;