import React from "react";
import Slider from "react-slick";
import { makeStyles } from '@material-ui/core/styles';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Tooltip } from "@material-ui/core";
import Zoom from '@material-ui/core/Zoom';

const useStyles = makeStyles((theme) => ({
    carouselItem: {
        padding: '0 10px',
        fontSize: '13px',
        minWidth: '50px',
        minHeight: '50px',
        userSelect: 'none',
        display: 'flex !important',
        justifyContent: 'center',
        alignItems: 'center',
        '&>a': {
            textDecoration: 'none',
            color: '#fff',
            '&>div': {
                display: 'flex !important',
                justifyContent: 'center',
                alignItems: 'center',
                backgroundColor: 'red',
                borderRadius: '50%',
                width: '50px',
                height: '50px',
                userSelect: 'none',
                transition: '200ms',
                '&:hover': {
                    width: '75px',
                    height: '75px',
                }
            },


        }
    }
}));



export default function Carousel() {

    let items = [];
    const classes = useStyles();

    (function setItems() {
        for (var i = 0; i < 100; i++) {
            const item =
                <div className={classes.carouselItem} key={i}>
                    <a href="/services">
                        <Tooltip
                            enterDelay={700}
                            leaveDelay={100}
                            TransitionComponent={Zoom}
                            title="message here"
                            arrow
                            interactive>
                            <div>{i + 1}</div></Tooltip>
                    </a>
                </div>;
            items.push(item);
        }
    })();





    var settings = {
        className: "slick-websites",
        dots: false,
        arrows: false,
        autoplay: true,
        centermode: true,
        infinite: true,
        speed: 500,
        centerPadding: '0px',
        adaptiveHeight: true,
        slidesToShow: 10,
        slidesToScroll: 1,
        initialSlide: 0,
        variableWidth: true,
        swipeToSlide: true,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 10,
                    infinite: true,
                }
            },
        ]
    };
    return (
        <Slider {...settings}>
            {items}
        </Slider>
    );
}