import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { Grid } from '@material-ui/core';
import clsx from 'clsx';

import VideoHolder from '../Fake/PlaceHolder';

const useStyles = makeStyles((theme) => ({
    root: {
        height: '132px',
        width: '232px',
        backgroundColor: 'lightgrey',
        padding: '8px',
    },
    main: {
        width: '100%',
        height: '100%',
        margin: 0,
    },
    content: {
        height: '100%',
    },
    wrapper: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100%'
    },
    sideBanner: {
        background: 'black',
        height: '100%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        fontSize: '22px',
        color: '#fafafa',
    },
    placer: {
        height: '100%',
        width: '100%'
    },
    columnContent: {
        maxWidth: '100%',
    },
    footerWrapper: {
        maxWidth: '100%',

    },
    topWrapper: {
        paddingBottom: '3px',
    },
    verticalPadder: {
        padding: '3px 0',
    },
    horizontalPadder: {
        '&+&': {
            paddingLeft: '3px'
        }
    },
    forceCenter: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    }
}));


export default function Banner(props) {
    const { type, num } = props;
    const classes = useStyles();

    if (type === 'side-banner') {
        return (
            <div className={classes.root}>
                <Grid className={classes.main} container>
                    <Grid className={clsx(classes.content, classes.forceCenter)} item xs={9}>
                        <Grid container spacing={1} direction="column">
                            <Grid item xs={12}>
                                <Grid container direction="row" spacing={1}>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={12}>
                                <Grid container direction="row" spacing={1}>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={12}>
                                <Grid container direction="row" spacing={1}>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={12}>
                                <Grid container direction="row" spacing={1}>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={12}>
                                <Grid container direction="row" spacing={1}>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={3} className={classes.wrapper}>
                        <Grid container className={classes.wrapper}>

                            <Grid item xs={11} className={classes.wrapper}>
                                <div className={classes.placer}>
                                    <div className={classes.sideBanner}>1</div>
                                </div>
                            </Grid>

                        </Grid>
                    </Grid>
                </Grid>
            </div>
        )
    }
    else if (type === "footer-banner") {
        if (num === 3) {
            return (
                <div className={classes.root}>
                    <Grid container direction="column" className={classes.main} alignItems="stretch">
                        <Grid item xs={9} className={classes.columnContent}>
                            <Grid container direction="column" spacing={1}>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs={3} className={classes.footerWrapper}>
                            <Grid container className={classes.placer} justify="space-around" alignItems="center">
                                <Grid item xs={4} className={clsx(classes.placer, classes.horizontalPadder)}>
                                    <div className={clsx(classes.placer, classes.verticalPadder)}>
                                        <div className={classes.sideBanner}>1</div>
                                    </div>
                                </Grid>
                                <Grid item xs={4} className={clsx(classes.placer, classes.horizontalPadder)}>
                                    <div className={clsx(classes.placer, classes.verticalPadder)}>
                                        <div className={classes.sideBanner}>2</div>
                                    </div>
                                </Grid>
                                <Grid item xs={4} className={clsx(classes.placer, classes.horizontalPadder)}>
                                    <div className={clsx(classes.placer, classes.verticalPadder)}>
                                        <div className={classes.sideBanner}>3</div>
                                    </div>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </div>
            )
        }
        else if (num === 2) {
            return (
                <div className={classes.root}>
                    <Grid container direction="column" className={classes.main} alignItems="stretch">
                        <Grid item xs={9} className={classes.columnContent}>
                            <Grid container direction="column" spacing={1}>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs={3} className={classes.footerWrapper}>
                            <Grid container className={classes.placer} justify="space-around" alignItems="center">
                                <Grid item xs={6} className={clsx(classes.placer, classes.horizontalPadder)}>
                                    <div className={clsx(classes.placer, classes.verticalPadder)}>
                                        <div className={classes.sideBanner}>1</div>
                                    </div>
                                </Grid>
                                <Grid item xs={6} className={clsx(classes.placer, classes.horizontalPadder)}>
                                    <div className={clsx(classes.placer, classes.verticalPadder)}>
                                        <div className={classes.sideBanner}>2</div>
                                    </div>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </div>
            )

        }
        else {
            return (
                <div className={classes.root}>
                    <Grid container direction="column" className={classes.main} alignItems="stretch">
                        <Grid item xs={9} className={classes.columnContent}>
                            <Grid container direction="column" spacing={1}>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12}>
                                    <Grid container direction="row" spacing={1}>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                        <Grid item xs={3}><VideoHolder /></Grid>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs={3} className={classes.footerWrapper}>
                            <Grid container className={classes.placer} justify="space-around" alignItems="center">
                                <Grid item xs={12} className={clsx(classes.placer, classes.horizontalPadder)}>
                                    <div className={clsx(classes.placer, classes.verticalPadder)}>
                                        <div className={classes.sideBanner}>1</div>
                                    </div>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </div>
            )
        }
    }
    else if (type === 'top-banner') {
        return (
            <div className={classes.root}>
                <Grid container direction="column-reverse" className={classes.main} alignItems="stretch">
                    <Grid item xs={9} className={classes.columnContent}>
                        <Grid container direction="column" spacing={1}>
                            <Grid item xs={12}>
                                <Grid container direction="row" spacing={1}>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={12}>
                                <Grid container direction="row" spacing={1}>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={12}>
                                <Grid container direction="row" spacing={1}>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={12}>
                                <Grid container direction="row" spacing={1}>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                    <Grid item xs={3}><VideoHolder /></Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={3} className={clsx(classes.footerWrapper, classes.topWrapper)}>
                        <Grid container className={classes.placer} justify="space-around" alignItems="center">
                            <Grid item xs={12} className={clsx(classes.placer, classes.horizontalPadder)}>
                                <div className={clsx(classes.placer)}>
                                    <div className={classes.sideBanner}>1</div>
                                </div>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </div>
        )
    }


}