
import React from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: '#02203c',
    },
    footerMenu: {
        padding: '20px',
        textAlign: 'center',
        borderTop: 'solid 1px #fafafa',
        justifyContent: 'center',
        alignItems: 'center',
    },
    footerList: {
        listStyle: 'none',
        display: 'flex',
        flexDirection: 'column',
        [theme.breakpoints.up('sm')]: {
            flexDirection: 'row',
        },
        justifyContent: 'center',
        alignItems: 'center',
        color: '#fafafa',
        textAlign: 'center',
        padding: 0,
    },
    footerMenuItem: {
        color: '#fafafa',
        textDecoration: 'none',
        cursor: 'pointer',
        userSelect: 'none',
        paddingBottom: '1px',
        '&:hover': {
            paddingBottom: 0,
            borderBottom: '1px solid #fafafa',
        },
        [theme.breakpoints.up('sm')]: {
            '&+$separator': {
                marginLeft: '20px',
            }
        }
    },
    separator: {
        width: 0,
        height: '16px',
        borderLeft: '0.5px solid #fafafa',
        display: 'none',
        [theme.breakpoints.up('sm')]: {
            display: 'block',
            '&+$footerMenuItem': {
                marginLeft: '20px',
            }
        },
    }
}));

function Footer(props) {
    const classes = useStyles();

    return (
        <Grid className={classes.root} container>
            <Grid item xs={false} sm={2}></Grid>
            <Grid container className={classes.footerMenu}>
                <ul className={classes.footerList}>
                    <Link className={classes.footerMenuItem} to="/contactus">
                        <li>Contact Us</li>
                    </Link>

                    <div className={classes.separator}></div>

                    <Link className={classes.footerMenuItem} to="/reviews">
                        <li>Reviews</li>
                    </Link>

                    <div className={classes.separator}></div>

                    <Link className={classes.footerMenuItem} to="/blog">
                        <li>Blog</li>
                    </Link>

                    <div className={classes.separator}></div>

                    <Link className={classes.footerMenuItem} to="/privacypolicy">
                        <li>Pirvacy Policy</li>
                    </Link>

                </ul>
            </Grid>
            <Grid item xs={false} sm={2}></Grid>
        </Grid>

    );
}
export default Footer;